
















import {
  defineComponent, PropType,
} from '@vue/composition-api';
import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { ImageData } from '@/app/shared/client/type/image';
import { ImageEditorControl } from '@/app/editor/client/view/component/control';
import useI18N from '@/app/shared/client/logic/i18n';
import {
  ImageContentEditorActionPayload,
} from '@/app/editor/client/type/editor-action/image-content-editor';

function useEmitEditorAction(
  emit: (event: string, payload: ImageContentEditorActionPayload) => void,
): (payload: ImageContentEditorActionPayload) => void {
  function onEditorAction(payload: ImageContentEditorActionPayload): void {
    emit('editor-action', payload);
  }

  return onEditorAction;
}

const ImageContentEditor = defineComponent({
  components: {
    ImageEditorControl,
  },
  props: {
    label: {
      required: true,
      type: String,
    },
    value: {
      required: false,
      default: undefined,
      type: Object as PropType<ImageData>,
    },
    tileId: {
      required: true,
      type: String,
    },
    imageId: {
      required: true,
      type: String,
    },
    thumbnailFit: {
      required: false,
      default: undefined,
      type: String,
    },
    thumbnailBackground: {
      required: false,
      default: undefined,
      type: String,
    },
  },
  setup(_, { emit, root }) {
    const store = root.$store as Store<State>;
    const i18n = useI18N(store);

    const onEditorAction = useEmitEditorAction(emit);

    return {
      i18n,
      onEditorAction,
    };
  },
});

export default ImageContentEditor;
